<template>
  <div class="input-stat">
    <span class="stat-name">
      {{ $t(`heroStats.${stat}`) }}
    </span>
    <InputNumber :min="0" :max="99" :value="value" @input="onInput" />
  </div>
</template>

<script>
import InputNumber from "@/components/base/InputNumber.vue";

export default {
  components: {
    InputNumber
  },
  props: {
    stat: {
      value: String,
      default: "",
      required: true
    },
    value: {
      value: Number,
      default: 0,
      required: true
    }
  },
  methods: {
    onInput(value) {
      this.$emit("input", value);
    }
  }
};
</script>

<style lang="scss">
.input-stat {
  .stat-name {
    display: block;
    margin-bottom: 10px;
  }
}
</style>
