<template>
  <div class="input-unit-effect">
    <input
      class="styled-checkbox"
      type="checkbox"
      v-model="selected"
      @click="onSelect"
    />
    <label>{{ name }}</label>
  </div>
</template>

<script>
export default {
  props: {
    name: {
      value: String,
      required: true,
      default: ""
    }
  },
  data() {
    return {
      selected: false
    };
  },
  methods: {
    onSelect() {
      this.selected = !this.selected;
      this.$emit("select", this.selected);
    }
  }
};
</script>

<style lang="scss" scoped>
.styled-checkbox {
  position: absolute; // take it out of document flow
  z-index: 2;
  opacity: 0; // hide it
}

input:checked {
  background: #f35429;

  label:before {
    background: #f35429;
  }
}

input {
  cursor: pointer;
}

label {
  position: relative;
  padding: 0;

  &:before {
    content: "";
    margin-right: 10px;
    display: inline-block;
    vertical-align: text-top;
    width: 20px;
    height: 20px;
    background: rgb(255, 255, 255);
    border: 1px solid #ced4da;
  }
}

input:checked + label:after {
  content: "";
  position: absolute;
  left: 5px;
  top: 9px;
  background: white;
  width: 2px;
  height: 2px;
  box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white,
    4px -6px 0 white, 4px -8px 0 white;
  transform: rotate(45deg);
}
</style>
