<template>
  <section class="damage-calculator-page">
    <section class="tabs">
      <div
        class="tab"
        v-for="(calculator, index) in calculators"
        :key="index"
        :class="{ active: index === activeCalculatorIndex }"
        :title="calculatorTitle(calculator)"
      >
        <div class="tab-title" @click="setActiveCalculator(index)">
          {{ calculatorTitle(calculator) }}
        </div>

        <div class="close-tab">
          <button @click="deleteCalculator(index)">Ã—</button>
        </div>
      </div>

      <button
        class="add-tab"
        :class="{ disabled: calculators.length >= 5 }"
        @click="addCalculator"
      >
        +
      </button>
    </section>

    <DamageCalculator
      v-for="(calculator, index) in calculators"
      :key="index"
      v-show="index === activeCalculatorIndex"
    />

    <PageFooter />
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import DamageCalculator from "@/components/DamageCalculator.vue";

export default {
  components: {
    DamageCalculator,
    PageFooter: () => import("@/components/PageFooter.vue")
  },
  mounted() {
    this.getHeroes();
    this.getCreatures();
    this.getTerrains();
  },
  computed: {
    ...mapGetters({
      calculators: "calculator/calculators",
      activeCalculatorIndex: "calculator/activeCalculatorIndex",
      attacker: "calculator/attacker",
      defender: "calculator/defender",
      options: "calculator/options"
    })
  },
  methods: {
    ...mapActions({
      getHeroes: "data/getHeroes",
      getCreatures: "data/getCreatures",
      getTerrains: "data/getTerrains",
      addCreature: "calculator/addCreature",
      setHeroAttack: "calculator/setHeroAttack",
      addCalculator: "calculator/addCalculator",
      setActiveCalculator: "calculator/setActiveCalculator",
      deleteCalculator: "calculator/deleteCalculator"
    }),
    calculatorTitle(calculator) {
      if (
        Object.entries(calculator.attacker.creature).length > 0 &&
        Object.entries(calculator.defender.creature).length > 0
      ) {
        return `${calculator.attacker.creature.name_en} - ${calculator.defender.creature.name_en}`;
      } else if (Object.entries(calculator.attacker.creature).length > 0) {
        return calculator.attacker.creature.name_en;
      } else if (Object.entries(calculator.defender.creature).length > 0) {
        return calculator.attacker.creature.name_en;
      } else {
        return this.$t("Damage calculator");
      }
    }
  }
};
</script>

