<template>
  <input
    type="number"
    :min="min"
    :max="max"
    :value="value"
    @input="onInput"
    @paste="onInput"
  />
</template>

<script>
export default {
  props: {
    min: {
      value: Number,
      default: 0,
      required: false
    },
    max: {
      value: Number,
      default: 99,
      required: false
    },
    value: {
      value: Number,
      default: 0,
      required: true
    }
  },
  data() {
    return {
      previousValue: 0
    };
  },
  methods: {
    onInput(event) {
      if (event.target.value < this.min || event.target.value > this.max) {
        event.target.value = this.previousValue;
      } else {
        event.target.value = parseInt(event.target.value);
        this.previousValue = event.target.value;
      }
      this.$emit("input", parseInt(event.target.value));
    }
  }
};
</script>
