<template>
  <button class="swap-sides">
    <FontAwesomeIcon
      class="fa-2x"
      icon="sync"
      title="Swap sides"
      @click="onClick"
    />
  </button>
</template>

<script>
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "SwapSidesButton",
  components: {
    FontAwesomeIcon
  },
  methods: {
    onClick() {
      this.$emit("click");
    }
  }
};
</script>
