/* eslint-disable prettier/prettier */
<template>
  <section class="damage-calculator">
    <section class="damage-calculator-body">
      <section class="attacker">
        <section class="header">
          <h2>{{ $t("Attacker") }}</h2>
          <button
            class="pick-creature-button attacker"
            @click="showAttackerModal = true"
          >
            {{ $t("Pick creature") }}
          </button>
        </section>

        <section class="unit-and-hero">
          <div class="unit">
            <button @click="showAttackerModal = true">
              <Portrait
                v-if="!attacker.creature.name.hasOwnProperty('en')"
                folder="/images/creatures_portraits"
                name="Archangel"
              />
              <Portrait
                v-else
                folder="/images/creatures_portraits"
                :name="attacker.creature.name.en"
              />
            </button>

            <InputNumber
              v-show="Object.entries(attacker.creature).length > 0"
              :min="0"
              :max="9999"
              :value="attacker.creature.count"
              @input="setCreaturesCount({ side: 'attacker', count: $event })"
            />
          </div>

          <div class="hero">
            <SelectHero
              v-if="Object.entries(attacker.creature).length > 0"
              :heroes="data.heroes"
              @select="setAttackerHero($event)"
            />

            <div
              class="hero-params"
              v-show="Object.entries(attacker.creature).length > 0"
            >
              <InputHeroStat
                class="level"
                stat="Level"
                :value="attacker.hero.stats.level"
                @input="
                  setHeroStat({
                    side: 'attacker',
                    stat: 'level',
                    value: $event
                  })
                "
              />
              <InputHeroStat
                class="attack"
                stat="Attack"
                :value="attacker.hero.stats.attack"
                @input="
                  setHeroStat({
                    side: 'attacker',
                    stat: 'attack',
                    value: $event
                  })
                "
              />
              <InputHeroStat
                class="defense"
                stat="Defense"
                :value="attacker.hero.stats.defense"
                @input="
                  setHeroStat({
                    side: 'attacker',
                    stat: 'defense',
                    value: $event
                  })
                "
              />
            </div>
          </div>
        </section>

        <section
          class="hero-skills"
          v-show="Object.entries(attacker.creature).length > 0"
        >
          <SelectSkill
            v-for="skill in $t('heroSkills.skills')"
            :key="skill"
            :skill="skill"
            @click="
              setHeroSkill({ side: 'attacker', skill: skill, value: $event })
            "
          />
        </section>

        <section
          class="unit-effects"
          v-show="Object.entries(attacker.creature).length > 0"
        >
          <div class="attack-buffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.attackPositiveEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>

          <div class="defend-buffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.defensePositiveEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>

          <div class="attack-debuffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.attackNegativeEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>
        </section>

        <section
          class="total-damage"
          v-show="Object.entries(attacker.creature).length > 0 && attacker.creature.count > 0"
        >
          <span>
            Damage: {{ `${attacker.minDamage} â€” ${attacker.maxDamage} (~ ${attacker.averageDamage})` }}
          </span>

          <span>
            Kills: {{ `${attacker.minKills} â€” ${attacker.maxKills} (~ ${attacker.averageKills})` }}
          </span>
        </section>
      </section>

      <section class="defender">
        <section class="header">
          <button
            class="pick-creature-button defender"
            @click="showDefenderModal = true"
          >
            {{ $t("Pick creature") }}
          </button>
          <h2>{{ $t("Defender") }}</h2>
        </section>

        <section class="unit-and-hero">
          <div class="hero">
            <SelectHero
              v-if="Object.entries(defender.creature).length > 0"
              :heroes="data.heroes"
            />

            <div
              class="hero-params"
              v-show="Object.entries(defender.creature).length > 0"
            >
              <InputHeroStat
                class="level"
                stat="Level"
                :value="defender.hero.stats.level"
                @input="setHeroStat({ side, stat, value: $event })"
              />
              <InputHeroStat
                class="attack"
                stat="Attack"
                :value="defender.hero.stats.level"
                @input="setHeroStat({ side, stat, value: $event })"
              />
              <InputHeroStat
                class="defense"
                stat="Defense"
                :value="defender.hero.stats.level"
                @input="setHeroStat({ side, stat, value: $event })"
              />
            </div>
          </div>

          <div class="unit">
            <button @click="showAttackerModal = true">
              <Portrait
                v-if="!defender.creature.name.hasOwnProperty('en')"
                folder="/images/creatures_portraits"
                name="Archangel"
              />
              <Portrait
                v-else
                folder="/images/creatures_portraits"
                :name="defender.creature.name.en"
              />
            </button>

            <InputNumber
              v-if="Object.entries(defender.creature).length > 0"
              :min="0"
              :max="9999"
              :value="defender.creature.count"
              @input="setCreaturesCount({ side: 'defender', count: $event })"
            />
          </div>
        </section>

        <section
          class="hero-skills"
          v-show="Object.entries(defender.creature).length > 0"
        >
          <SelectSkill
            v-for="skill in $t('heroSkills.skills')"
            :key="skill"
            :skill="skill"
            @click="
              setHeroSkill({ side: 'defender', skill: skill, value: $event })
            "
          />
        </section>

        <section
          class="unit-effects"
          v-show="Object.entries(defender.creature).length > 0"
        >
          <div class="attack-buffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.attackPositiveEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'defender', effect })
                  : removeUnitEffect({ side: 'defender', effect })
              "
            />
          </div>

          <div class="defend-buffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.defensePositiveEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'defender', effect })
                  : removeUnitEffect({ side: 'defender', effect })
              "
            />
          </div>

          <div class="attack-debuffs">
            <InputUnitEffect
              v-for="effect in $t('unitEffects.attackNegativeEffects')"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'defender', effect })
                  : removeUnitEffect({ side: 'defender', effect })
              "
            />
          </div>
        </section>

        <section
          class="total-damage"
          v-show="Object.entries(defender.creature).length > 0 && defender.creature.count > 0"
        >
          <span>
            Damage: {{ `${defender.minDamage} â€” ${defender.maxDamage} (~ ${defender.averageDamage})` }}
          </span>

          <span>
            Kills: {{ `${defender.minKills} â€” ${defender.maxKills} (~ ${defender.averageKills})` }}
          </span>
        </section>
      </section>
    </section>

    <section class="damage-calculator-footer">
      <SwapSidesButton />
      <SelectTerrain class="select-terrain" :terrains="data.terrains" />
    </section>

    <SelectUnitModal
      v-if="showAttackerModal"
      :showModal="showAttackerModal"
      :creatures="data.creatures"
      @close="showAttackerModal = false"
      @select="setActiveCreature({ side: 'attacker', creature: $event })"
    />
    <SelectUnitModal
      v-if="showDefenderModal"
      :showModal="showDefenderModal"
      :creatures="data.creatures"
      @close="showDefenderModal = false"
      @select="setActiveCreature({ side: 'defender', creature: $event })"
    />
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import SelectUnitModal from "@/components/damageCalculator/SelectUnitModal.vue";
import SelectSkill from "@/components/damageCalculator/SelectSkill.vue";
import InputUnitEffect from "@/components/damageCalculator/InputUnitEffect.vue";
import InputHeroStat from "@/components/damageCalculator/InputHeroStat.vue";
import InputNumber from "@/components/base/InputNumber.vue";
import SwapSidesButton from "@/components/damageCalculator/SwapSidesButton";

export default {
  components: {
    SelectUnitModal,
    SelectSkill,
    InputUnitEffect,
    InputHeroStat,
    SelectHero: () => import("@/components/damageCalculator/SelectHero.vue"),
    Portrait: () => import("@/components/damageCalculator/Portrait.vue"),
    SelectTerrain: () =>
      import("@/components/damageCalculator/SelectTerrain.vue"),
    InputNumber,
    SwapSidesButton
  },
  data() {
    return {
      showAttackerModal: false,
      showDefenderModal: false
    };
  },
  watch: {
    calculationStarted() {
      if (this.calculationStarted) {
        this.calculate(this.attacker, this.defender);
        this.calculate(this.defender, this.attacker);
      }
    }
  },
  computed: {
    ...mapGetters({
      activeCalculator: "calculator/activeCalculator",
      attacker: "calculator/attacker",
      defender: "calculator/defender",
      options: "calculator/options",
      calculationStarted: "calculator/calculationStarted",
      data: "data/data"
    })
  },
  methods: {
    ...mapActions({
      addCreature: "calculator/addCreature",
      setHeroAttack: "calculator/setHeroAttack",
      setCreaturesCount: "calculator/setCreaturesCount",
      setActiveCreature: "calculator/setActiveCreature",
      setAttackerHero: "calculator/setAttackerHero",
      setHeroStat: "calculator/setHeroStat",
      setTotalDamages: "calculator/setTotalDamages",
      stopCalculation: "calculator/stopCalculation"
    }),
    calculate(attacker, defender) {
      if (this.attacker.creature.count > 0 && this.defender.creature.count > 0) {
        let attackerSide = {
          ...attacker,
          creature: Object.assign({}, this.attacker.creature),
          hero: {
            ...this.attacker.hero,
            name: Object.assign({}, this.attacker.creature.name)
          }
        };
        let defenderSide = {
          ...defender,
          creature: Object.assign({}, this.attacker.creature),
          hero: Object.assign({}, this.attacker.hero)
        };

        console.log('hehehehe', attackerSide, defenderSide);

        attackerSide = this.calculationWaterfall(attackerSide, defenderSide);
        defenderSide = this.calculationWaterfall(defenderSide, attackerSide);

        // console.log(attackerSide, defenderSide);

        this.setTotalDamages({ side: "attacker", damages: attackerSide });
        this.setTotalDamages({ side: "defender", damages: defenderSide });
      }

      this.stopCalculation();
    },
    calculationWaterfall(firstSide, secondSide) {
      // console.log('before', firstSide)

      // firstSide = this.blessModificator(firstSide);
      // console.log('bless', firstSide)

      // firstSide = this.curseModificator(firstSide);
      // console.log('curse', firstSide)

      // firstSide = this.statsModificators(firstSide, secondSide, this.options.terrain);
      // console.log('stats', firstSide)

      // firstSide = this.totalDamagesModificators(firstSide, secondSide);
      // console.log('totalDamages', firstSide)

      // firstSide = this.hatesModificator(firstSide, this.data.hates);
      // console.log('hates', firstSide)

      // firstSide = this.ifArtilleryCreature(firstSide);
      // console.log('artillery', firstSide)

      // firstSide = this.unitsFeaturesModificator(firstSide);
      // console.log('features', firstSide)

      // firstSide = this.mainFormula(firstSide, secondSide);
      // console.log('main', firstSide)

      return firstSide;
    },

    blessModificator(side) {
      if (side.creature.effects.includes("Bless")) {
        if (side.hero.specialtySpell === "Bless") {
          side.creature.damageBonus += (0.03 * side.hero.stats.level) / side.creature.level;
        }
        if (side.hero.skills.water <= 1) side.creature.minDamage = side.creature.maxDamage;
        else if (hero.skills.water >= 2) {
          side.creature.minDamage = side.creature.maxDamage + 1;
          side.creature.maxDamage++;
        }
      }
      return side;
    },
    curseModificator(side) {
      if (side.creature.effects.includes("Curse")) {
        if (side.hero.skills.fire <= 1) side.creature.maxDamage = side.creature.minDamage;
        else if (side.hero.skills.fire >= 2) {
          side.creature.minDamage = side.creature.minDamage * 0.8 - 1;
          side.creature.maxDamage = side.creature.minDamage;
        }
      }
      return side;
    },
    statsModificators(firstSide, secondSide, terrain) {
      console.log('test', firstSide.creature, firstSide.creature)
      firstSide.creature.attack += parseInt(firstSide.hero.attack);
      firstSide.creature.defense += parseInt(firstSide.hero.defense);


      if (firstSide.creature.nativeTerrain === terrain.id) {
        firstSide.creature.attack++;
        firstSide.creature.defense++;
      }

      // ATTACKER hero unit specialty bonus
      if (firstSide.hero.hasOwnProperty("specialtyUnit")) {
        if (firstSide.hero.specialtyUnit.includes(firstSide.creature.id)) {
          firstSide.creature.attack *= 1 + (firstSide.hero.stats.level / firstSide.creature.level) * 0.05;
          firstSide.creature.defense *= 1 + (firstSide.hero.stats.level / firstSide.creature.level) * 0.05;

          firstSide.creature.attack = Math.ceil(firstSide.creature.attack);
          firstSide.creature.defense = Math.ceil(firstSide.creature.defense);
        }
      }

      if (firstSide.creature.effects.length > 0) {
        // Bloodlust spell
        if (firstSide.creature.effects.includes("Bloodlust")) {
          if (firstSide.hero.skills.fire <= 1)
            firstSide.creature.attack += 3;
          else if (firstSide.hero.skills.fire >= 2)
            firstSide.creature.attack += 6;
        }

        // Frenzy spell
        if (firstSide.creature.effects.includes("Frenzy")) {
          if (firstSide.hero.skills.fire <= 1)
            firstSide.creature.attack += firstSide.creature.defense;
          else if (firstSide.hero.skills.fire === 2)
            firstSide.creature.attack += firstSide.creature.defense * 1.5;
          else if (firstSide.hero.skills.fire === 3)
            firstSide.creature.defense = 0;

          firstSide.creature.attack += firstSide.creature.defense * 2;
        }

        // Slayer spell
        if (firstSide.creature.effects.includes("Slayer")) {
          let slayerCreatures = [26, 27, 54, 55, 68, 69, 96, 97, 110, 111, 137, 138, 139, 140];
          if (firstSide.hero.skills.fire === 2)
            slayerCreatures.push(12, 13, 82, 83);
          else if (firstSide.hero.skills.fire === 3)
            slayerCreatures.push(40, 41, 124, 125, 154, 155);

          if (slayerCreatures.includes(secondSide.creature.id))
            firstSide.creature.attack += 8;
        }

        // Prayer spell
        if (firstSide.creature.effects.includes("Prayer")) {
          if (firstSide.hero.skills.water <= 1) {
            firstSide.creature.attack += 2
            firstSide.creature.defense += 2
          }
          else if (firstSide.hero.skills.water >= 2) {
            firstSide.creature.attack += 2
            firstSide.creature.defense += 4;
          }
        }

        // Precision spell
        if (firstSide.creature.effects.includes("Precision") && firstSide.creature.ranged) {
          if (firstSide.hero.skills.air <= 1)
            firstSide.creature.attack += 3;
          else if (firstSide.hero.skills.air >= 2)
            firstSide.creature.attack += 6;
        }

        // Stone skin spell
        if (firstSide.creature.effects.includes("Stone Skin")) {
          if (firstSide.hero.skills.earth <= 1)
            firstSide.creature.defense += 3;
          else if (firstSide.hero.skills.earth >= 2)
            firstSide.creature.defense += 6;
        }

        // Weakness spell
        if (firstSide.creature.effects.includes("Weakness")) {
          if (secondSide.hero.skills.water <= 1)
            firstSide.creature.attack -= 3;
          else if (secondSide.hero.skills.water >= 2)
            firstSide.creature.attack -= 6;
        }

        // Disrupting Ray spell
        if (firstSide.creature.effects.includes("Disrupting Ray")) {
          if (secondSide.hero.skills.air <= 1)
            firstSide.creature.defense -= 3;
          else if (secondSide.hero.skills.air === 2)
            firstSide.creature.defense -= 4;
          else if (secondSide.hero.skills.air === 3)
            firstSide.creature.defense -= 5;
        }

        // Hero specialty bonus
        if (firstSide.hero.hasOwnProperty("specialtySpell")) {
          switch (firstSide.hero.specialtySpell) {
            case "Bloodlust":
              if (firstSide.creature.level <= 2)
                firstSide.creature.attack += 3;
              else if (firstSide.creature.level <= 4)
                firstSide.creature.attack += 2;
              else if (firstSide.creature.level <= 6)
                firstSide.creature.attack += 1;
              break;
            case "Prayer":
              if (firstSide.creature.level <= 2) {
                firstSide.creature.attack += 3;
                firstSide.creature.defense += 3;
              } else if (firstSide.creature.level <= 4) {
                firstSide.creature.attack += 2;
                firstSide.creature.defense += 2;
              } else if (firstSide.creature.level <= 6) {
                firstSide.creature.attack += 1;
                firstSide.creature.defense += 1;
              }
              break;
            case "Precision":
              if (firstSide.creature.level <= 2)
                firstSide.creature.attack += 3;
              else if (firstSide.creature.level <= 4)
                firstSide.creature.attack += 2;
              else if (firstSide.creature.level <= 6)
                firstSide.creature.attack += 1;
              break;
            case "Stone Skin":
              if (firstSide.creature.level <= 2)
                firstSide.creature.defense += 3;
              else if (firstSide.creature.level <= 4)
                firstSide.creature.defense += 2;
              else if (firstSide.creature.level <= 6)
                firstSide.creature.defense += 1;
              break;
            case "Weakness":
              if (firstSide.creature.level <= 2)
                firstSide.creature.attack -= 3;
              else if (firstSide.creature.level <= 4)
                firstSide.creature.attack -= 2;
              else if (firstSide.creature.level <= 6)
                firstSide.creature.attack -= 1;
              break;
            case "Disrupting Ray":
              firstSide.creature.defense -= 2;
            default:
              break;
          }
        }
      }

      return firstSide;
    },
    totalDamagesModificators(firstSide, secondSide) {
      let levelBonus,
        damageBonus,
        defenseBonus,
        defenseMagicBonus = 0;

      console.log(firstSide)

      if (firstSide.hero.hasOwnProperty("level")) {
        levelBonus = 1 + 0.05 * firstSide.hero.stats.level;
      }

      // Offense skill bonus
      if (firstSide.hero.skills.offense > 0 && !firstSide.creature.ranged) {
        let bonus = 0.1 * firstSide.hero.skills.offense;

        if (firstSide.hero.hasOwnProperty("specialtySkill")) {
          if (firstSide.hero.specialtySkill === "offense") {
            bonus *= levelBonus;

            if (bonus > 1.92)
              bonus = 1.92;
          }
        }
        damageBonus += bonus;
      }

      // Armorer skill bonus
      if (firstSide.hero.skills.armorer > 0) {
        let bonus = 0.05 * firstSide.hero.skills.armorer;
        if (firstSide.hero.hasOwnProperty("specialtySkill")) {
          if (firstSide.hero.specialtySkill === "armorer") {
            bonus *= levelBonus;

            if (bonus > 0.7)
              bonus = 0.7;
          }
        }
        defenseBonus += bonus;
      }

      // Archery skill bonus
      if (firstSide.hero.skills.archery > 0 && firstSide.creature.ranged) {
        let bonus = 0;
        if (firstSide.hero.skills.archery === 1)
          bonus = 0.1;
        else if (firstSide.hero.skills.archery === 2)
          bonus = 0.25;
        else if (firstSide.hero.skills.archery === 3)
          bonus = 0.5;

        if (firstSide.hero.hasOwnProperty("specialtySkill")) {
          if (firstSide.hero.specialtySkill === "archery") {
            bonus *= levelBonus;

            if (bonus > 3.2)
              bonus = 3.2;
          }
        }
        damageBonus += bonus;
      }

      // Artillery skill bonus (for ballista and cannon only)
      if (firstSide.hero.skills.artillery > 0 && (firstSide.creature.id === 158 || firstSide.creature.id === 159)) {
        if (firstSide.hero.skills.artillery === 3) {
          firstSide.minDamage *= 2;
          firstSide.maxDamage *= 2;
        }
      }

      // Shield spell
      if (firstSide.creature.effects.includes("Shield")) {
        if (firstSide.hero.skills.earth <= 1)
          defenseMagicBonus += 0.15;
        else if (firstSide.hero.skills.earth >= 2)
          defenseMagicBonus += 0.3;
      }

      if (firstSide.creature.effects.includes("Air Shield") && secondSide.creature.ranged) {
        if (firstSide.hero.skills.air < 2)
          defenseMagicBonus += 0.25;
        else if (firstSide.hero.skills.air > 1)
          defenseMagicBonus += 0.5;
      }

      return {
        ...firstSide,
        damageBonus,
        defenseBonus,
        defenseMagicBonus
      };
    },
    hatesModificator(side, hates) {
      let damageBonus = 0;
      const hate = hates.filter(creatureHate => creatureHate.id === side.creature.id);
      if (hate.length > 0) {
        damageBonus = 0.5

        if (side.creature.hasOwnProperty("damageBonus")) {
          creature.damageBonus += damageBonus;
        }
      }
      return side;
    },
    ifArtilleryCreature(side) {
      if (side.creature.id === 158) {
        side.creature.minDamage += (side.creature.attack - side.hero.stats.attack) * 2;
        side.creature.maxDamage += (side.creature.attack - side.hero.stats.attack) * 3;
      } else if (side.creature.id === 159) {
        side.creature.minDamage += (side.creature.attack - side.hero.stats.attack) * 4;
        side.creature.maxDamage += (side.creature.attack - side.hero.stats.attack) * 7;
      }
      return side
    },
    unitsFeaturesModificator(side) {
      switch (side.creature.id) {
        case 110:
          side.creature.defense *= 0.6; // Behemoth
          break;
        case 111:
          side.creature.defense *= 0.2; // Ancient Behemoth
          break;
        case 152:
          side.creature.attack *= 0.7; // Nix
          break;
        case 153:
          side.creature.attack *= 0.4; // Nix-Warrior
          break;
      }
      return side;
    },
    mainFormula(firstSide, secondSide) {
      // If ATTACKER attack > DEFENDER defense
      // Then base formula: ATTACKER units count * ATTACKER unit damage * (1 + 0.05 * (ATTACKER attack - DEFENDER defense))

      // If DEFENDER attack > ATTACKER defense
      // Then base formula:
      // DEFENDER units count * DEFENDER unit damage * (1 + 0.05 * (DEFENDER attack - ATTACKER defense))

      // Else if DEFENDER attack < ATTACKER defense
      // Then base formula:
      // DEFENDER units count * DEFENDER unit damage * (1 - 0.025 * (ATTACKER defense - DEFENDER attack))
      let damageBonus, defenseBonus, defenseMagicBonus = 0

      console.log('a', firstSide, secondSide)

      if (firstSide.creature.attack > secondSide.creature.defense) {
        // Max attack cap = 3
        let difference = Math.abs(0.05 * (firstSide.creature.attack - secondSide.creature.defense));

        if (difference > 3) {
          difference = 3;
        }

        damageBonus = 1 + difference + firstSide.creature.damageBonus;
        defenseBonus = 1 - secondSide.creature.defenseBonus;
        defenseMagicBonus = 1 - secondSide.creature.defenseMagicBonus;
      } else if (firstSide.creature.attack === secondSide.creature.defense) {
        damageBonus = 1 + firstSide.creature.damageBonus;
        defenseBonus = 1 - secondSide.creature.defenseBonus;
        defenseMagicBonus = 1 - secondSide.creature.defenseMagicBonus;
      } else if (firstSide.attack < secondSide.defense) {
        let difference = Math.abs((secondSide.defense - firstSide.attack) * 0.025);

        if (difference > 0.7) {
          difference = 0.7;
        }

        damageBonus = 1 - difference + firstSide.creature.damageBonus;
        defenseBonus = 1 - secondSide.creature.defenseBonus;
        defenseMagicBonus = 1 - secondSide.creature.defenseMagicBonus;
      }
      firstSide.creature.minDamage = Math.abs(firstSide.creature.minDamage * (damageBonus * defenseBonus * defenseMagicBonus));
      firstSide.creature.maxDamage = Math.abs(firstSide.creature.maxDamage * (damageBonus * defenseBonus * defenseMagicBonus));

      firstSide.minDamage = Math.floor(firstSide.minDamage * firstSide.count);
      firstSide.maxDamage = Math.floor(firstSide.maxDamage * firstSide.count);
      firstSide.averageDamage = Math.floor((firstSide.minDamage + firstSide.maxDamage) / 2);

      firstSide.minKills = Math.floor(firstSide.minDamage / secondSide.creature.health);
      firstSide.maxKills = Math.floor(firstSide.maxDamage / secondSide.creature.health);
      firstSide.averageKills = Math.floor(firstSide.averageDamage / secondSide.creature.health);

      return firstSide;
    }
  }
};
</script>

